body {
  font-family: 'jaf-domus', sans-serif; }

h1, h2, h3 {
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700; }

h1, h2 {
  font-size: 28px;
  line-height: 32px; }
  @media (min-width: 768px) {
    h1, h2 {
      font-size: 34px;
      line-height: 40px; } }

h3 {
  font-size: 20px;
  line-height: 26px; }

p {
  font-size: 18px;
  line-height: 26px;
  font-weight: 400; }
  @media (min-width: 768px) {
    p {
      font-size: 20px; } }

h2 .svg-inline--fa {
  vertical-align: baseline; }

.description--header {
  color: #fff;
  margin-bottom: 2rem; }

.text-white-transparent {
  color: rgba(255, 255, 255, 0.7); }

.footer__title {
  margin: 0 0 10px 0;
  font-size: 25px;
  text-transform: uppercase;
  color: #fff; }

.footer__list {
  list-style: none;
  padding-left: 0; }

.footer__link {
  margin-bottom: 8px;
  font-size: 21px;
  color: #fff; }
  .footer__link:hover {
    color: #fff; }

.vertical-line {
  background-color: #74bfe7;
  position: absolute;
  left: 0;
  height: 2px;
  width: 100%;
  top: 48px; }
  @media (min-width: 768px) {
    .vertical-line {
      width: 2px;
      height: 100%;
      left: 55px;
      top: 0; } }

.nav-link__number {
  font-family: 'Titillium Web', sans-serif;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  height: 56px;
  width: 56px;
  border-radius: 56px;
  background-color: #f4f9fb;
  color: #74bfe7;
  border: 2px solid #74bfe7;
  transition: background-color .35s ease, color .35s ease, border-color .35s ease;
  will-change: background-color, color, border-color; }
  .nav-link:hover .nav-link__number,
  .active .nav-link__number {
    background-color: #39a4dc;
    border-color: #39a4dc;
    color: #fff; }

.nav-link__text {
  font-family: 'Titillium Web', sans-serif;
  display: none;
  margin-left: 10px;
  text-transform: none;
  color: #74bfe7;
  font-size: 15px;
  transition: font-size .35s ease;
  will-change: font-size; }
  .active .nav-link__text {
    color: #39a4dc;
    font-size: 20px; }
  @media (min-width: 768px) {
    .nav-link__text {
      display: inline-block; } }

.svg--nav-pills {
  text-align: center; }
  .svg--nav-pills > svg {
    display: inline-block;
    width: 180px; }
    @media (min-width: 768px) {
      .svg--nav-pills > svg {
        width: 70%; } }

@media (min-width: 768px) {
  .tab-content--smooth-fade {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%; }
    .tab-content--smooth-fade .tab-pane {
      display: block !important;
      position: absolute;
      opacity: 0;
      transition: opacity .35s ease-in-out;
      will-change: opacity; }
      .tab-content--smooth-fade .tab-pane.active {
        opacity: 1; } }

.section--roadmap .nav-link__number {
  font-size: 20px; }

.section--roadmap h3 {
  margin: 0;
  color: #556992;
  font-size: 30px;
  padding-bottom: 10px;
  font-weight: 700; }

.roadmap-image {
  display: none; }
  @media (min-width: 768px) {
    .roadmap-image {
      display: flex;
      align-items: center; }
      .roadmap-image > img {
        width: 100%; } }

.card--person-image {
  height: 100%; }
  .card--person-image .card-body {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .card--person-image a:hover {
    text-decoration: none; }
  .card--person-image .card-top-image {
    border-radius: 10rem;
    height: 10rem;
    width: 10rem;
    margin: -1.25rem auto .75rem;
    background-image: none; }

.partner-image {
  display: flex;
  justify-content: center;
  align-items: stretch; }
  @media (min-width: 768px) {
    .partner-image {
      margin-bottom: 1rem; } }

.partner-link {
  padding: 2rem;
  margin: 1rem;
  border-radius: .25rem;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  background-color: white; }
  .partner-link img {
    max-height: 4rem;
    max-width: 100%; }

a.partner-link {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1);
  will-change: filter, transform; }
  a.partner-link:hover {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2); }
  a.partner-link:active, a.partner-link:focus {
    box-shadow: none; }

.fnd-page-container {
  max-width: 1920px; }
  .fnd-page-container::after {
    display: block;
    content: "";
    position: absolute;
    z-index: -3;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: #fff; }

.fnd-header {
  max-width: 1920px; }
  .fnd-header .container {
    display: flex;
    justify-content: space-between; }

.section--steps p {
  color: #545556; }

.section--steps h3 {
  margin: 0;
  color: #23a7e0;
  font-size: 30px;
  padding-bottom: 30px;
  font-weight: 700; }

.section--steps::before {
  background-position-x: -12rem; }

.section--supporters {
  padding: 15rem 0 5rem 0; }

.section--partnership-logos {
  height: 215px;
  padding: 0 0 75px 0;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center; }
  @media (min-width: 768px) {
    .section--partnership-logos {
      height: 140px;
      padding-bottom: 0; } }

@media (max-width: 767.98px) {
  .section--team .nav-pills {
    display: none; } }

@media (max-width: 767.98px) {
  .section--team .tab-pane {
    display: flex !important; } }

.section--team .section__title {
  color: rgba(0, 0, 0, 0.54);
  margin: 0; }

.section--team .nav-link {
  border-radius: 0;
  border-bottom: 2px solid transparent;
  padding-bottom: 10px;
  margin: 0 20px; }
  .section--team .nav-link:hover {
    border-bottom-color: rgba(0, 0, 0, 0.54); }
  .section--team .nav-link.active, .section--team .nav-link.active:hover {
    border-bottom-color: #556992; }
  .section--team .nav-link.active .section__title {
    color: #556992; }

.section__title {
  color: #556992;
  font-weight: 700;
  text-align: center; }
  .section__title--info {
    margin-bottom: 3rem; }
    @media (min-width: 768px) {
      .section__title--info {
        margin-bottom: 4rem; } }

.scrolltonext {
  position: absolute;
  display: flex;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  bottom: 0;
  height: 50px;
  width: 100%;
  left: 0;
  margin: 0 auto;
  z-index: 99; }
  @media (min-width: 768px) {
    .scrolltonext {
      height: 115px; } }
  .scrolltonext--top {
    top: 0;
    bottom: auto; }
  .scrolltonext:hover, .scrolltonext:active, .scrolltonext:focus {
    text-decoration: none; }
  .scrolltonext::after {
    width: 6px;
    height: 48px;
    display: block;
    content: "";
    background-image: url("../img/scrollblue.png");
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat; }
  .scrolltonext--header {
    text-align: center;
    height: 80px;
    align-items: start; }
    @media (min-width: 768px) {
      .scrolltonext--header {
        height: 110px; } }
    .scrolltonext--header::after {
      background-image: url("../img/scroll.png"); }

.triangle {
  width: 100%;
  position: absolute; }
  .triangle::after {
    display: block;
    content: "";
    margin: 0 auto;
    width: 1920px;
    height: 0;
    border-style: solid;
    border-width: 0;
    border-color: transparent;
    border-left-width: 1920px; }
  .triangle--inverse::after {
    border-right-width: 1920px;
    border-left-width: 0; }
  .triangle--top {
    top: 0; }
    .triangle--top::after {
      border-top: 7rem solid #fff; }
  .triangle--bottom {
    bottom: 0; }
    .triangle--bottom::after {
      border-bottom: 7rem solid #fff; }
  .triangle--light::after {
    border-top-color: #f4f9fb;
    border-bottom-color: #f4f9fb; }
